<template>
  <div class="modal_wrapper" @click="modalClose">
    <div class="modal_container">
      <h2>--- {{ fixType(form_infos.fix_type) }}内容確認画面 ---</h2>
      <div class="modal_box">
        <table>
          <tr>
            <th>テナント</th>
            <td class="td_center">{{ tenantval }}</td>
            <td width="5%" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">-</td>
          </tr>
          <tr>
            <th>区分</th>
            <td class="td_center">{{ fixType(form_infos.fix_type) }}</td>
            <td width="5%" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">-</td>
          </tr>
          <tr>
            <th>契約名</th>
            <td class="td_center">{{ form_infos.contractor_name }}</td>
            <td class="td_judge" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">
              <font-awesome-icon v-if="validate('null', 'contractor_name')" class="icon_error vali_icon" icon="circle-xmark" />
              <font-awesome-icon v-else-if="validate('length', ['contractor_name', 30])" class="icon_error vali_icon" icon="circle-xmark" />
              <font-awesome-icon v-else class="icon_info vali_icon" icon="circle-check" />
              <div class="error_detail">{{ detailList.contractor_name }}</div>
            </td>
          </tr>
          <tr>
            <th>接続デバイス上限数</th>
            <td class="td_center" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">{{ form_infos.allow_device_num }}</td>
            <td class="td_center" v-else>{{ origin_infos.allow_device_num }}</td>
            <td class="td_judge" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">
              <font-awesome-icon v-if="validate('null', 'allow_device_num')" class="icon_error vali_icon" icon="circle-xmark" />
              <font-awesome-icon v-else-if="validate('allow', 'allow_device_num')" class="icon_error vali_icon" icon="circle-xmark" />
              <font-awesome-icon v-else-if="validate('diff', 'allow_device_num')" class="icon_warn vali_icon" icon="circle-exclamation" />
              <font-awesome-icon v-else class="icon_info vali_icon" icon="circle-check" />
              <div class="error_detail">{{ detailList.allow_device_num }}</div>
            </td>
          </tr>
          <tr>
            <th>サブドメイン</th>
            <td class="td_center" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">{{ form_infos.domain }}</td>
            <td class="td_center" v-else>{{ origin_infos.domain }}</td>
            <td class="td_judge" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">
              <font-awesome-icon v-if="validate('diff', 'domain')" class="icon_warn vali_icon" icon="circle-exclamation" />
              <font-awesome-icon v-else class="icon_info vali_icon" icon="circle-check" />
              <div class="error_detail">{{ detailList.domain }}</div>
            </td>
          </tr>
          <tr>
            <th>APB 機能</th>
            <td class="td_center" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">{{ (form_infos.apb_mode_flag) ? '有効' : '無効' }}</td>
            <td class="td_center" v-else>{{ (origin_infos.apb_mode_flag) ? '有効' : '無効' }}</td>
            <td class="td_judge" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">
              <font-awesome-icon v-if="validate('diff', 'apb_mode_flag')" class="icon_warn vali_icon" icon="circle-exclamation" />
              <font-awesome-icon v-else class="icon_info vali_icon" icon="circle-check" />
              <div class="error_detail">{{ detailList.apb_mode_flag }}</div>
            </td>
          </tr>
          <tr>
            <th>入退管理 機能</th>
            <td v-if="(form_infos.fix_type !== 'delete'  && form_infos.fix_type !== 'init') && form_infos.ee_mode_flag" class="td_center">
              有効
              <div>備考名1[{{ (form_infos.ee_desc_1) ? form_infos.ee_desc_1 : '備考1' }}] 備考名2[{{ (form_infos.ee_desc_2) ? form_infos.ee_desc_2 : '備考2' }}]</div>
            </td>
            <td v-else-if="(form_infos.fix_type == 'delete' && form_infos.fix_type !== 'init') && origin_infos.ee_mode_flag" class="td_center">
              有効
              <div>備考名1[{{ (origin_infos.ee_desc_1) ? origin_infos.ee_desc_1 : '備考1' }}] 備考名2[{{ (origin_infos.ee_desc_2) ? origin_infos.ee_desc_2 : '備考2' }}]</div>
            </td>
            <td v-else class="td_center">無効</td>
            <td class="td_judge" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">
              <font-awesome-icon v-if="validate('diff', 'ee_mode_flag')" class="icon_warn vali_icon" icon="circle-exclamation" />
              <font-awesome-icon v-else class="icon_info vali_icon" icon="circle-check" />
              <div class="error_detail">{{ detailList.ee_mode_flag }}</div>
            </td>
          </tr>
          <tr>
            <th>IP制限（API）</th>
            <td class="font_ip td_center" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">{{ form_infos.api_allow_ip }}</td>
            <td class="font_ip td_center" v-else>{{ origin_infos.api_allow_ip }}</td>
            <td class="td_judge" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">
              <font-awesome-icon v-if="validate('diff', 'api_allow_ip')" class="icon_warn vali_icon" icon="circle-exclamation" />
              <font-awesome-icon v-else class="icon_info vali_icon" icon="circle-check" />
              <div class="error_detail">{{ detailList.api_allow_ip }}</div>
            </td>
          </tr>
          <tr>
            <th>トライアル期間</th>
            <td class="td_center" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">{{ form_infos.trial_limit }}</td>
            <td class="td_center" v-else>{{ origin_infos.trial_limit }}</td>
            <td class="td_judge" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">
              <font-awesome-icon v-if="validate('diff', 'trial_limit')" class="icon_warn vali_icon" icon="circle-exclamation" />
              <font-awesome-icon v-else class="icon_info vali_icon" icon="circle-check" />
              <div class="error_detail">{{ detailList.trial_limit }}</div>
            </td>
          </tr>
          <tr>
            <th>管理者ログインID</th>
            <td class="td_center" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">{{ form_infos.login_id }}</td>
            <td class="td_center" v-else>{{ origin_infos.login_id }}</td>
            <td class="td_judge" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">
              <font-awesome-icon v-if="validate('null', 'login_id')" class="icon_error vali_icon" icon="circle-xmark" />
              <font-awesome-icon v-else-if="validate('length', ['login_id', 30])" class="icon_error vali_icon" icon="circle-xmark" />
              <font-awesome-icon v-else-if="validate('under', 'login_id')" class="icon_error vali_icon" icon="circle-xmark" />
              <font-awesome-icon v-else-if="form_infos.fix_type == 'add' && validate('duplicate', 'login_id')" class="icon_error vali_icon" icon="circle-xmark" />
              <font-awesome-icon v-else class="icon_info vali_icon" icon="circle-check" />
              <div class="error_detail">{{ detailList.login_id }}</div>
            </td>
          </tr>
          <tr v-if="form_infos.fix_type == 'add'">
            <th>管理者ログイン名</th>
            <td class="td_center" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">{{ form_infos.user_name }}</td>
            <td class="td_center" v-else>{{ origin_infos.user_name }}</td>
            <td class="td_judge" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">
              <font-awesome-icon v-if="validate('null', 'user_name')" class="icon_error vali_icon" icon="circle-xmark" />
              <font-awesome-icon v-else-if="validate('length', ['user_name', 30])" class="icon_error vali_icon" icon="circle-xmark" />
              <font-awesome-icon v-else class="icon_info vali_icon" icon="circle-check" />
              <div class="error_detail">{{ detailList.user_name }}</div>
            </td>
          </tr>
          <tr>
            <th>IP制限（GUI）</th>
            <td class="font_ip td_center" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">{{ form_infos.allow_ips }}</td>
            <td class="font_ip td_center" v-else>{{ origin_infos.allow_ips }}</td>
            <td class="td_judge" v-if="form_infos.fix_type !== 'delete' && form_infos.fix_type !== 'init'">
              <font-awesome-icon v-if="validate('diff', 'allow_ips')" class="icon_warn vali_icon" icon="circle-exclamation" />
              <font-awesome-icon v-else class="icon_info vali_icon" icon="circle-check" />
              <div class="error_detail">{{ detailList.allow_ips }}</div>
            </td>
          </tr>
        </table>
      </div>
      <div>
        <div class="btn" @click="submitAction">処理実行</div>
      </div>
    </div>
  </div>
</template>

<script>
const axios = require('axios')
export default {
  data () {
    return {
      msgList: {
        'null':'未入力もしくは不正値',
        'length':'文字数超過',
        'allow':'テナント合計接続数の超過',
        'diff':'差分の発生',
        'under':'アンダーバー使用不可',
        'duplicate':'テナント内ID重複'
      },
      detailList: {
        'contractor_name' : '正常値',
        'allow_device_num' : '正常値',
        'domain' : '正常値',
        'apb_mode_flag' : '正常値',
        'ee_mode_flag' : '正常値',
        'api_allow_ip' : '正常値',
        'trial_limit' : '正常値',
        'login_id' : '正常値',
        'user_name' : '正常値',
        'allow_ips' : '正常値'
      }
    }
  },
  props: {
    tenantval: String,
    form_infos: Array,
    origin_infos: Array,
    count: Number,
    users: Array
  },
  methods: {
    fixType: function (type) {
      switch (type) {
        case 'add':
          return '登録'
        case 'edit':
          return '更新'
        case 'delete':
          return '削除'
        case 'init':
          return '初期化'
        default:
          return ''
      }
    },
    modalClose: function (e) {
      let self = this
      if (!e.target.closest('.modal_container')) {
        self.$emit('input', false)
      }
    },
    validate: function (vali_type, value_type) {
      let self = this
      let result = false

      switch (vali_type) {
        case 'null': // 値の不正値確認
          if (!self.form_infos[value_type]) {
            result = true
          }
          break
        case 'length': // 文字数上限確認
          if (self.form_infos[value_type[0]].length > value_type[1]) {
            result = true
          }
          break
        case 'allow': // テナントの接続上限確認
          if (100 < self.count + Number(self.form_infos[value_type])) {
            result = true
          }
          break
        case 'under': // アンダーバーが含まれているか確認
          if (self.form_infos[value_type].match(/_/g)) {
            result = true
          }
          break
        case 'duplicate': // テナント内のログインIDと重複していないか
          for (let i = 0; i < self.users.length; i++) {
            if (self.form_infos[value_type] === self.users[i].login_id) {
              result = true
              break
            }
          }
          break
        case 'diff': // 差分発生確認
          if (self.form_infos.fix_type === 'edit') {
            if (self.form_infos[value_type] != self.origin_infos[value_type]) {
              result = true
            } else if (value_type == 'ee_mode_flag' && self.form_infos[value_type]) { // 入退管理専用の分岐
              if (self.form_infos.ee_desc_1 !== self.origin_infos.ee_desc_1 || self.form_infos.ee_desc_2 !== self.origin_infos.ee_desc_2) {
                result = true
              }
            }
          }
          break
        default:
          break
      }

      if (result) self.detailList[value_type] = self.msgList[vali_type]
      return result

    },
    submitAction: function () {
      let self = this

      if (document.getElementsByClassName('icon_error').length) {
        // エラーがある場合は処理しない
        alert('エラー箇所があります。確認してください。')
      } else {
        if (self.form_infos.fix_type !== 'delete' && self.form_infos.fix_type !== 'init') {
          // 登録もしくは更新処理の場合
          let confirm_msg = '[ '+ self.tenantval +' ]へ'
          confirm_msg += (self.form_infos.fix_type === 'add') ? '登録' : '更新'
          confirm_msg += '処理を開始します'
          confirm_msg += (document.getElementsByClassName('icon_warn').length) ? '\r\n　・内容更新箇所 [ ' + document.getElementsByClassName('icon_warn').length + ' ]' : ''
          confirm_msg += (self.form_infos.trial_limit) ? '\r\n　・デモ環境です（期限[ ' + self.form_infos.trial_limit + ' ]）' : ''
          if (confirm(confirm_msg)) {
            // ロード画面表示
            self.$emit('loading', true)

            if (self.form_infos.fix_type === 'add') {
              // 登録処理
              axios
              .post(location.origin + '/server/backend/contractor/' + self.tenantval, self.form_infos)
              .then(function (response) {
                let msg = response.data.msg
                msg += '\r\n※※必ず下記を控えてください※※'
                msg += '\r\n▼contractor_id'
                msg += '\r\n' + response.data.contractor_id
                msg += '\r\n▼api_token'
                msg += '\r\n' + response.data.api_token
                msg += '\r\n '
                msg += '\r\n※api_tokenはこの後コピーを行います'
                alert(msg)

                // api_tokenをコピーする
                navigator.clipboard.writeText(response.data.api_token)

                // 2秒後に初期画面へリダイレクト
                setTimeout(() => {
                  alert('api_tokenのコピー完了をご確認ください\r\n※クリップボード許可のポップアップが出ている場合は許可をしてください')
                  window.location.reload()
                }, 2000)
              })
              .catch(function (e) {
                alert(e.response.data.msg)

                // ロード画面非表示
                self.$emit('loading', false)
              })
            } else {
              // 更新処理
              axios
              .post(location.origin + '/server/backend/contractor/' + self.tenantval + '/' + self.origin_infos.contractor_id, self.form_infos)
              .then(function (response) {
                alert(response.data.msg)

                // 初期画面へリダイレクト
                window.location.reload()
              })
              .catch(function (e) {
                alert(e.response.data.msg)

                // ロード画面非表示
                self.$emit('loading', false)
              })
            }
          }
        } else {
          // 削除もしくは初期化処理だった場合
          let title = self.fixType(self.form_infos.fix_type)
          let delete_msg = '※' + title + '内容を確認してください※\r\n' + title + '対象テナント【' + self.tenantval + '】\r\n' + title + '契約名【' + self.form_infos.contractor_name + '】'
          if (confirm(delete_msg)) {
            // 再度確認する
            delete_msg += '\r\n \r\n※※' + title + 'がされると復元できませんがよろしいですか？※※\r\n \r\n実行する場合は「' + title + '開始」と入力しOKを押してください。'
            if (self.form_infos.fix_type == 'delete' && prompt(delete_msg) === '削除開始') {
              // ロード画面表示
              self.$emit('loading', true)

              axios
                .delete(location.origin + '/server/backend/delete/' + self.tenantval + '/' + self.origin_infos.contractor_id)
                .then(function (response) {
                  alert(response.data.msg)

                  // 初期画面へリダイレクト
                  window.location.reload()
                })
                .catch(function (e) {
                  alert(e.response.data.msg)

                  // ロード画面非表示
                  self.$emit('loading', false)
                })
            } else if (self.form_infos.fix_type == 'init' && prompt(delete_msg) === '初期化開始') {
              // ロード画面表示
              self.$emit('loading', true)

              axios
                .delete(location.origin + '/server/backend/init/' + self.tenantval + '/' + self.origin_infos.contractor_id)
                .then(function (response) {
                  alert(response.data.msg)

                  // 初期画面へリダイレクト
                  window.location.reload()
                })
                .catch(function (e) {
                  alert(e.response.data.msg)

                  // ロード画面非表示
                  self.$emit('loading', false)
                })
            } else {
              alert('「削除開始」がキャンセルされました')
            }
          }
        }
      }
    }
  }
}
</script>

<style>
table {
  border-collapse: collapse;
  font-size: 1.2rem;
  width: 100%;
  max-width: 800px;
}

th, td {
  border: 2px solid #fff;
  background-color: #e6f1f6;
  padding: 0.3em 1em;
}

th {
  background-color: #003da6;
  color: #fff;
  font-weight: bold;
  text-align: center;
  border-radius: 15px 0 0 15px;
  width: 20%;
  max-width: 20%;
  min-width: 150px;
  position: relative;
  font-size: 1rem;
}

th::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translateY(-50%);
  border: 8px solid transparent;
  border-left: 12px solid #003da6;
  z-index: 2;
}

th::after {
  content: '';
  position: absolute;
  top: 50%;
  left: calc(100% + 2px);
  transform: translateY(-50%);
  border: 10px solid transparent;
  border-left: 14px solid #fff;
  z-index: 1;
}

.td_center {
  width: 50%;
  max-width: 400px;
}
.td_judge {
  width: 5%;
  position: relative;
}

.modal_wrapper {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
}

.modal_container {
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 1rem 100px;
  box-sizing: border-box;
  height: 80%;
  width: 60%;
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow-y: auto;
}

.modal_box {
  width: 100%;
  max-width: 800px;
  margin-bottom: 2rem;
}

.font_ip {
  font-size: 0.8rem;
  overflow-x: scroll;
}

.ee_desc {
  overflow-x: scroll;
  width: 70px
}

.icon_info {
  color: rgb(50, 200, 50);
}
.icon_warn {
  color: rgb(248, 161, 0);
}
.icon_error {
  color: rgb(255, 120, 120);
}

.error_detail {
  opacity: 0;
  pointer-events: none;
  background-color: beige;
  z-index: 20;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 12em;
  font-size: 0.6rem;
  text-align: center;
  padding: 0.2rem 0.5rem;
  border-radius: 0 100px 100px 100px;
  box-shadow: #000 2px 2px 4px;
}

.td_judge:hover:nth-child(n) .error_detail:nth-child(n) {
  opacity: 1;
  transition: opacity 0.5s ease;
}
</style>
