<template>
  <LoadingModal v-if="loading"/>
  <form action="#" name="facet_admin_form">
    <CurrentModal
      v-if="modal_state"
      :tenantval="tenantval"
      :form_infos="form_infos"
      :origin_infos="origin_infos"
      :count="count"
      :users="users"
      @input="modal_state = $event"
      @loading="loading = $event"
    />
    <div class="main_content">
      <h2>テナント</h2>
      <div class="form_wrapper">
        <div class="flex_box">
          <div class="form_container">
            <h3>テナント選択<span class="required">*</span></h3>
            <select name="tenant" id="selectTenant" v-model="tenantval" @change="getContractors">
              <option value=""></option>
              <option v-for="tenant in tenants" :value="tenant" v-bind:key="tenant">{{ tenant }}</option>
            </select>
          </div>
          <div v-if="tenantval !== ''" class="type_select_wrapper">
            <h3>区分<span class="required">*</span></h3>
            <div class="type_select_container">
              <div class="type_select_box">
                <input type="radio" name="fix_type" value="add" v-model="type" @change="valueInit(true)">
                <label>追加</label>
              </div>
              <div class="type_select_box">
                <input type="radio" name="fix_type" value="edit" v-model="type" @change="valueInit(true)">
                <label>更新</label>
              </div>
              <div class="type_select_box">
                <input type="radio" name="fix_type" value="delete" v-model="type" @change="valueInit(true)">
                <label>削除</label>
              </div>
              <div class="type_select_box">
                <input type="radio" name="fix_type" value="init" v-model="type" @change="valueInit(true)">
                <label style="font-size: 1rem;">初期化</label>
              </div>
            </div>
          </div>
        </div>
    </div>
    <div v-if="tenantval !== '' && type !== ''" >
        <h2>契約関連</h2>
        <div class="form_wrapper">
          <div v-if="type === 'edit' || type === 'delete' || type === 'init'" class="form_container">
            <h3>契約名<span class="required">*</span></h3>
            <select name="contractor_id" class="main_text" @change="selectContractor">
              <option value=""></option>
              <option v-for="cont in contractors" :value="cont.contractor_id" v-bind:key="cont.contractor_id">{{ cont.contractor_name }}</option>
            </select>
          </div>
          <div v-else class="form_container">
            <h3>契約名<span class="required">*</span></h3>
            <input type="text" name="contractor_name" class="main_text">
          </div>
          <div v-if="(type === 'edit' && contractor != null) && confirmPermission() == 'admin'" class="form_container">
            <h3>ds-api-token</h3>
            <input type="text" :value="contractor.api_token" class="main_text" readonly style="font-size: 0.5rem;">
          </div>
          <div v-if="type !== 'delete' && type !== 'init'" class="flex_box">
            <div class="form_container relative">
              <h3>接続デバイス上限数<span class="required">*</span></h3>
              <input v-if="type !== 'edit' || contractor == null" type="number" name="allow_device_num">
              <input v-else-if="form_infos !== null" type="number" name="allow_device_num" :value="form_infos.allow_device_num">
              <input v-else type="number" name="allow_device_num" :value="contractor.allow_device_num">
              <p class="allow_device">※{{ tenantval }}の合計接続台数[ <span class="allow_device_count">{{ count }}</span> ]</p>
            </div>
          </div>
          <div v-if="type !== 'delete' && type !== 'init'" class="form_container">
            <h3>サブドメイン</h3>
            <input v-if="type !== 'edit' || contractor == null" type="text" name="domain" class="main_text">
            <input v-else-if="form_infos !== null" type="text" name="domain" class="main_text" :value="form_infos.domain">
            <input v-else type="text" name="domain" class="main_text" :value="contractor.domain">
          </div>
          <div v-if="type !== 'delete' && type !== 'init'" class="check_container">
            <h3>APB 機能</h3>
            <div v-if="type !== 'edit' || contractor == null" class="ckeck_box">
              <div>
                <input type="checkbox" id="checkAPB" name="apb_mode_flag"/>
                <label for="checkAPB"></label>
              </div>
            </div>
            <div v-else class="ckeck_box">
              <div>
                <input type="checkbox" id="checkAPB" name="apb_mode_flag" :checked="contractor.apb_mode_flag"/>
                <label for="checkAPB"></label>
              </div>
            </div>
          </div>
          <div v-if="type !== 'delete' && type !== 'init'" class="flex_box">
            <div class="check_container">
              <h3>入退管理 機能</h3>
              <div v-if="type !== 'edit' || contractor == null" class="ckeck_box">
                <div>
                  <input type="checkbox" id="checkEE" name="ee_mode_flag" @change="changeEEStatus"/>
                  <label for="checkEE"></label>
                </div>
              </div>
              <div v-else class="ckeck_box">
                <div>
                  <input type="checkbox" id="checkEE" name="ee_mode_flag" :checked="contractor.enter_exit_mode_flag" @change="changeEEStatus"/>
                  <label for="checkEE"></label>
                </div>
              </div>
            </div>
            <div v-if="ee_flag" class="ee_desc_box" id="eeDescBox">
              <input v-if="type !== 'edit' || contractor == null" type="text" name="ee_description_name_1" class="sub_text" placeholder="備考名1">
              <input v-else-if="form_infos !== null" type="text" name="ee_description_name_1" class="sub_text" placeholder="備考名1" :value="form_infos.ee_desc_1">
              <input v-else type="text" name="ee_description_name_1" class="sub_text" placeholder="備考名1" :value="contractor.enter_exit_description_name1">
              <input v-if="type !== 'edit' || contractor == null" type="text" name="ee_description_name_2" class="sub_text" placeholder="備考名2">
              <input v-else-if="form_infos !== null" type="text" name="ee_description_name_2" class="sub_text" placeholder="備考名2" :value="form_infos.ee_desc_2">
              <input v-else type="text" name="ee_description_name_2" class="sub_text" placeholder="備考名2" :value="contractor.enter_exit_description_name2">
            </div>
          </div>
          <div v-if="type !== 'delete' && type !== 'init'" class="form_container">
            <h3>IP制限（API）</h3>
            <textarea v-if="type !== 'edit' || contractor == null" type="text" name="api_allow_ip" class="main_text" rows="3"></textarea>
            <textarea v-else-if="form_infos !== null" type="text" name="api_allow_ip" class="main_text" :value="form_infos.api_allow_ip" rows="3"></textarea>
            <textarea v-else type="text" name="api_allow_ip" class="main_text" :value="contractor.api_allow_ip" rows="3"></textarea>
          </div>
          <div v-if="type !== 'delete' && type !== 'init'" class="form_container">
            <h3>トライアル期間（終了日時）</h3>
            <input v-if="type !== 'edit' || contractor == null" type="datetime-local" name="trial_limit" class="main_text">
            <input v-else-if="form_infos !== null" type="datetime-local" name="trial_limit" class="main_text" :value="datetimeConversion(form_infos.trial_limit, 'value')">
            <input v-else type="datetime-local" name="trial_limit" class="main_text" :value="datetimeConversion(contractor.trial_limit, 'value')">
          </div>
        </div>
        <h2 v-if="type !== 'delete' && type !== 'init'">ログイン情報</h2>
        <div v-if="type !== 'delete' && type !== 'init'" class="form_wrapper">
          <div class="form_container">
            <h3>管理者ログインID<span class="required">*</span></h3>
            <input v-if="type !== 'add'" type="text" name="login_id" class="main_text disable_content" :value="login_id">
            <input v-else type="text" name="login_id" class="main_text">
          </div>
          <div v-if="type === 'add'" class="form_container">
            <h3>管理者ログイン名<span class="required">*</span></h3>
            <input type="text" name="user_name" class="main_text">
          </div>
          <input v-else type="hidden" name="user_name">
          <div class="form_container">
            <h3>IP制限（GUI）</h3>
            <textarea v-if="type !== 'edit' || user == []" type="text" name="allow_ips" class="main_text" rows="3"></textarea>
            <textarea v-else-if="form_infos !== null" type="text" name="allow_ips" class="main_text" :value="form_infos.allow_ips" rows="3"></textarea>
            <textarea v-else type="text" name="allow_ips" class="main_text" :value="user.allow_ips" rows="3"></textarea>
          </div>
        </div>
        <div class="submit_box">
          <div class="btn" @click="openModal">内容確認</div>
        </div>
      </div>
      <div v-else></div>
    </div>
  </form>
</template>

<script>
const axios = require('axios')
import CurrentModal from '@/components/CurrentModal.vue'
import LoadingModal from '@/components/LoadingModal.vue'

export default {
  name: 'TopPage',
  components: {
    CurrentModal,
    LoadingModal
  },
  data () {
    return {
      tenantval: '',
      tenants: ['on-premises'],
      type: '',
      ee_flag: false,
      contractors: [],
      contractor: null,
      count: 0,
      user: [],
      users: [],
      login_id: '',
      form_infos: null,
      origin_infos: null,
      modal_state: false,
      error: '',
      loading: false
    }
  },
  methods: {
    getContractors: function () {
      let self = this

      if (self.tenantval !== '') {
        axios
          .get(location.origin + '/server/backend/contractor/' + self.tenantval)
          .then(function (response) {
            self.contractors = response.data
            self.count = 0

            for (let i = 0; i < self.contractors.length; i++) self.count += self.contractors[i].allow_device_num
          })
        
        // userの取得
        axios
          .get(location.origin + '/server/backend/user/' + self.tenantval)
          .then(function (response) {
            self.users = response.data
          })
      }

      self.valueInit()
    },
    selectContractor: function (e) {
      let self = this

      // フォームと格納されている変数初期化
      self.valueInit()
      self.form_infos = null

      // contractorの取得
      for (let i = 0; i < self.contractors.length; i++) {
        if (self.contractors[i].contractor_id === Number(e.target.value)) {
          // 一致したら格納・フラグ更新して終了
          self.contractor = self.contractors[i]
          self.ee_flag = Boolean(self.contractor.enter_exit_mode_flag)
          break
        } else {
          // 一致しない場合は続ける
          continue
        }
      }

      // userの取得
      axios
        .get(location.origin + '/server/backend/user/' + self.tenantval + '/' + e.target.value)
        .then(function (response) {
          self.user = response.data
          self.login_id = response.data.login_id
        })
    },
    valueInit: function (change = false) {
      let self = this

      // 変数の初期化
      self.contractor = null
      self.user = []
      self.login_id = ''
      self.ee_flag = false

      // 値の初期化
      if (document.facet_admin_form.login_id) {
        document.facet_admin_form.allow_device_num.value = ''
        document.facet_admin_form.domain.value = ''
        document.facet_admin_form.apb_mode_flag.checked = false
        document.facet_admin_form.ee_mode_flag.checked = false
        document.facet_admin_form.api_allow_ip.value = ''
        document.facet_admin_form.trial_limit.value = ''
        document.facet_admin_form.login_id.value = ''
        document.facet_admin_form.user_name.value = ''
        document.facet_admin_form.allow_ips.value = ''
        if (document.getElementById('eeDescBox')) {
          document.facet_admin_form.ee_description_name_1.value = ''
          document.facet_admin_form.ee_description_name_2.value = ''
        }
      }

      // fix_typeをeditもしくはdeleteに変更した場合にcontractor_idを初期化
      if (change && self.type !== 'add') document.facet_admin_form.contractor_id.value = ''
    },
    changeEEStatus: function () {
      let self = this
      let form = document.facet_admin_form
      let ee_check = document.getElementById('checkEE').checked

      // ee_flag変更前にform状況を格納
      if (self.type === 'edit') {
        self.form_infos = {
          'allow_device_num' : form.allow_device_num.value,
          'domain' : form.domain.value,
          'api_allow_ip' : form.api_allow_ip.value,
          'trial_limit' : self.datetimeConversion(form.trial_limit.value, 'param'),
          'allow_ips' : form.allow_ips.value
        }
      }

      // チェックステータスを反映
      self.ee_flag = ee_check
    },
    openModal: function () {
      let self = this
      let form = document.facet_admin_form

      // tenantval、type選択しているか
      if (self.type !== 'add' && form.contractor_id.value == '') {
        alert('契約名が選択されていません')
      } else {
        // フォーム内容の格納
        if (form.fix_type.value !== 'delete' && form.fix_type.value !== 'init') {
          let contractor_name = (form.fix_type.value == 'add') ? form.contractor_name.value : self.contractor.contractor_name
          self.form_infos = {
            'fix_type' : form.fix_type.value,
            'contractor_name' : contractor_name,
            'allow_device_num' : (form.allow_device_num.value) ? Number(form.allow_device_num.value) : null,
            'domain' : (form.domain.value) ? form.domain.value : null,
            'apb_mode_flag' : (form.apb_mode_flag.checked) ? 1 : 0,
            'ee_mode_flag' : (form.ee_mode_flag.checked) ? 1 : 0,
            'ee_desc_1' : (form.ee_mode_flag.checked && form.ee_description_name_1.value) ? form.ee_description_name_1.value : null,
            'ee_desc_2' : (form.ee_mode_flag.checked && form.ee_description_name_2.value) ? form.ee_description_name_2.value : null,
            'api_allow_ip' : (form.api_allow_ip.value) ? form.api_allow_ip.value : null,
            'trial_limit' : (form.trial_limit.value) ? self.datetimeConversion(form.trial_limit.value, 'param') : null,
            'login_id' : (form.login_id.value) ? form.login_id.value : null,
            'user_name' : (form.user_name.value) ? form.user_name.value : null,
            'allow_ips' : (form.allow_ips.value) ? form.allow_ips.value : null
          }
        } else {
          self.form_infos = {
            'fix_type' : form.fix_type.value,
            'contractor_name' : self.contractor.contractor_name
          }
        }
  
        // 既存内容の整理・格納
        if (form.fix_type.value !== 'add') {
          self.origin_infos = {
            'contractor_id' : self.contractor.contractor_id,
            'contractor_name' : self.contractor.contractor_name,
            'allow_device_num' : self.contractor.allow_device_num,
            'domain' : self.contractor.domain,
            'apb_mode_flag' : (self.contractor.apb_mode_flag) ? 1 : 0,
            'ee_mode_flag' : (self.contractor.enter_exit_mode_flag) ? 1 : 0,
            'ee_desc_1' : self.contractor.enter_exit_description_name1,
            'ee_desc_2' : self.contractor.enter_exit_description_name2,
            'api_allow_ip' : self.contractor.api_allow_ip,
            'trial_limit' : self.contractor.trial_limit,
            'login_id' : self.user.login_id,
            'user_name' : self.user.user_name,
            'allow_ips' : self.user.allow_ips
          }
        } else {
          self.origin_infos = null
        }
  
        self.modal_state = true
      }
    },
    datetimeConversion: function (datetime, type) {
      if (!datetime) {
        return datetime
      } else {
        if (type === 'value') {
          // フォームバリューとして使用したい場合
          let datetime_array = datetime.split(' ')
          let time_array = datetime_array[1].split(':')
          return datetime_array[0] + 'T' + time_array[0] + ':' + time_array[1]
        } else {
          // パラメータもしくは表示用として使用したい場合
          return datetime.replace('T', ' ') + ':00'
        }
      }
    },
    confirmPermission: function () {
      return document.getElementById('basicLoginUser').textContent
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h2, h3 {
  margin: 0;
  text-align: start;
}

input, select {
  padding: 0.5rem 1rem;
  border-radius: 10px;
}

input, select, label {
  font-size: 1.2rem;
}

select {
  width: fit-content;
}

input[type=text] {
  box-sizing: border-box;
}
input[type=number] {
  width: 25%;
}
input[type=checkbox] {
  height: 30px;
  width: 30px;
  cursor: pointer;
}
input[type=datetime-local] {
  box-sizing: border-box;
}

textarea {
  resize: none;
}

.relative {
  position: relative;
}

.main_content {
  background-color: #fff;
  box-sizing: border-box;
  padding: 1rem 150px;
  width: 100%;
}

.form_wrapper {
  padding: 0 0 2rem 50px;
}

.form_container {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin: 1rem 0 2rem 0;
}

.check_container {
  align-items: center;
  display: flex;
  width: 40%;
  margin: 1rem 0;
}
.check_container > h3 {
  width: 70%;
}
.main_text {
  width: 100%;
  min-width: 300px;
}

.ee_desc_box {
  align-items: center;
  display: flex;
  width: 50%;
}
.ee_desc_box > input:nth-child(2) {
  margin-left: 1rem;
}
.sub_text {
  width: 150px !important;
}

.required {
  color: red;
  font-weight: bold;
}

.flex_box {
  align-items: center;
  display: flex;
}

.submit_box {
  display: flex;
  justify-content: center;
}

.allow_device {
  font-size: 0.9rem;
  margin: 0;
  position: absolute;
  bottom: 10%;
  left: calc(150px + 10%);
}
.allow_device_count {
  font-size: 1.2rem;
  font-weight: bold;
}

</style>
