<template>
  <div class="header">
    <h1>facet契約管理画面</h1>
    <h3>ログイン「<span id="basicLoginUser">{{ user }}</span>」</h3>
  </div>
</template>

<script>
const axios = require('axios')
export default {
  data () {
    return {
      user: ''
    }
  },
  mounted () {
    console.log(location.host);
    axios
      .get(location.origin + '/server/backend/basic')
      .then(response => (this.user = response.data))
  }
}
</script>

<style>

.header {
  align-items: center;
  box-sizing: border-box;
  color: #fff;
  background-color: #003da6;
  display: flex;
  padding: 1rem 3rem;
  justify-content: space-between;
  width: 100%;
}

</style>
